import React, { useEffect, useLayoutEffect, useState } from 'react';
import globalStyles from '@common/style';
import courses from '@data/courses';

import '@components/SingleCourse/index.scss';

//TYPES
import { CourseType } from '@data/interfaces/courses.interfaces';
import WhiteLoader from '@components/Loader/WhiteLoader';
import { connect } from 'react-redux';
import _ from 'lodash';

type NewCourseProps = {
    courseNumber?: number;
    window?: {
        width: number;
        height: number;
    }
}

const NewCourseData: CourseType = courses[0];


const NewCourseComponent: React.FunctionComponent<NewCourseProps> = (props) => {
    const ratio = 7 / 4;
    const [courseNumber, setCourseNumber] = useState<number | undefined>(props?.courseNumber);

    const filteredCourses = courses.filter((course) => {
        if (course?.free) {
            return false;
        }
        return true
    });
    useEffect(() => {
        if (courseNumber !== undefined) {
            setCourseNumber(props?.courseNumber);
        } else {
            setCourseNumber(_.random(0, filteredCourses.length - 1));
        }
    }, [])

    let NewCourseData: any = null;

    NewCourseData = filteredCourses[courseNumber || 0]

    const [calculated, setCalculated] = useState<boolean>(false);
    const [videoWidth, setVideoWidth] = useState<string>("");
    const [videoHeight, setVideoHeight] = useState<string>("");

    const convertVideoWidthAndHeight = () => {
        let absoluteVideoWidth = 0, videoWidth = "", videoHeight = "";
        if (window.innerWidth < 576) {
            absoluteVideoWidth = window.screen.width * 0.529
        } else if (window.innerWidth >= 576 && window.innerWidth < 768) {
            absoluteVideoWidth = 269
        } else if (window.innerWidth >= 768 && window.innerWidth < 992) {
            absoluteVideoWidth = 375
        } else if (window.innerWidth >= 992 && window.innerWidth < 1200) {
            absoluteVideoWidth = 230
        } else if (window.innerWidth >= 1200) {
            absoluteVideoWidth = 270
        } else {
            absoluteVideoWidth = 270;
        }
        videoWidth = `${absoluteVideoWidth * ratio}px`;
        videoHeight = `${absoluteVideoWidth}px`;
        setVideoWidth(videoWidth);
        setVideoHeight(videoHeight);
        setCalculated(true);
    }


    const prepareButtonText = () => {
        if (NewCourseData?.free) {
            return `Ücretsiz Olarak Kursa Kayıt Ol`
        }
        else {
            return `<span style="text-decoration:line-through"}}>${NewCourseData?.price}</span> yerine ${NewCourseData?.discountedPrice}'ye Kursa Kaydol`
        }
    }

    useEffect(() => {
        convertVideoWidthAndHeight();
    }, [])

    useLayoutEffect(() => {
        convertVideoWidthAndHeight();
    }, [props.window?.width, props.window?.height])

    if (!calculated) {
        return <div className="middle-content" style={{
            background: NewCourseData?.primaryColor || "#215733",
            padding: "10px 0px 10px 0px",
            minHeight: "350px",
            display: "flex",
            alignItems: "center",
            justifyContent: "center"
        }}> <WhiteLoader loaded={false} />
        </div>
    }

    const prepareCourseLink = (): string => {
        const referral: string = NewCourseData?.referralCode ? "?referralCode=" + NewCourseData?.referralCode : "?";
        let coupon: string = "";
        if (referral === "?") {
            coupon = NewCourseData?.couponCode ? ("couponCode=" + NewCourseData?.couponCode) : "";
        } else {
            coupon = NewCourseData?.couponCode ? ("&couponCode=" + NewCourseData?.couponCode) : "";
        }
        return `${NewCourseData?.url}${referral}${coupon}`
    }

    return (
        <div className="one-course" style={{
            background: NewCourseData?.primaryColor || "#215733"
        }}>
            <div className="container">
                <div className="course-container">
                    <div className="course-video">
                        {NewCourseData?.youtubeEmbedUrl ? (
                            <div className="video">
                                <div style={{
                                    maxHeight: `calc(${videoHeight})`,
                                }}>
                                    <iframe width={videoWidth}
                                        height={videoHeight}
                                        src={`${NewCourseData?.youtubeEmbedUrl || "https://www.youtube.com/embed/h-Xx14d39vk"}?fs=1&iv_load_policy=3&showinfo=0&rel=0&cc_load_policy=0&start=0&end=0&mute=0&loop=1&color=white&controls=0&modestbranding=1&playsinline=1&rel=0&enablejsapi=1`}
                                        title="YouTube video player"
                                        frameBorder="0"
                                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; allowFullScreen;"
                                        allowFullScreen={true}></iframe>
                                </div>
                            </div>

                        ) : (
                            <div className="image">
                                <img src={NewCourseData?.thumbnail || "https://via.placeholder.com/300x300"} alt="New Course" style={{
                                    width: videoWidth,
                                    height: videoHeight,
                                }} />
                            </div>
                        )}

                    </div>
                    <div className="course-description">
                        <div className="course">
                            <div className="innerDetail">
                                <div className="tag" style={{
                                    border: `1px solid ${NewCourseData?.secondaryColor}`,
                                }}>
                                    {NewCourseData?.tag}
                                </div>
                                <div className="title" style={{
                                    color: NewCourseData?.secondaryColor
                                }}>
                                    {NewCourseData?.title}
                                </div>
                                <div className="description" style={{
                                    color: NewCourseData?.secondaryColor
                                }}>
                                    {NewCourseData?.description}
                                </div>
                                <div className="link">
                                    <p className="paragraph">
                                        <a
                                            href={prepareCourseLink()}
                                            //href="https://www.udemy.com/course/nodejs-egitimi-sifirdan-profesyonele/?referralCode=0017AFE73990096201B4&couponCode=CANGOKCEASLAN"
                                            target="_blank"
                                            className="btn btn-course"
                                            style={{
                                                borderColor: NewCourseData?.secondaryColor,
                                            }} dangerouslySetInnerHTML={{
                                                __html: prepareButtonText()
                                            }}>
                                        </a>
                                    </p>
                                </div>
                            </div>
                            <div>

                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </div>
    )
}

export default connect((state: any) => ({
    window: state.window.sizes,
}), {

})(NewCourseComponent);
